<template>
    <div class="wrapper">
        <div class="content" v-if="Priority">
            <div>
                <div style="padding:5px 0 0 5px;">
                    <el-button type="primary" size="mini" @click="addClick()">添加</el-button>
                </div>
                <el-table :data="tableData" style="width: 100%">
                    <el-table-column prop="UserName" label="用户名" width="280">
                    </el-table-column>
                    <el-table-column prop="priority" label="权限" width="280">
                    </el-table-column>
                    <el-table-column fixed="right" label="操作" width="200">
                        <template slot-scope="scope">
                            <el-button @click="handleClick(scope.row,Priority)" type="text" size="small">修改</el-button>
                            <el-button @click.native.prevent="deleteRow(scope.$index,scope.row,tableData)" v-if="scope.row.priority!=0" type="text" size="small">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
        <div class="content" v-else>
            <div>
                <el-table :data="tableData" style="width: 100%">
                    <el-table-column prop="UserName" label="用户名" width="300">
                    </el-table-column>
                    <el-table-column prop="priority" label="权限" width="300">
                    </el-table-column>
                    <el-table-column fixed="right" label="操作" width="200">
                        <template slot-scope="scope">
                            <el-button @click="handleClick(scope.row,Priority)" type="text" size="small">修改</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
        <el-dialog title="添加用户" :visible.sync="dialogVisible" :append-to-body="true" width="500px" :close-on-click-modal="false">
            <div>
                <table style="width:100%;">
                    <tr>
                        <td align="right" width="32%">用户名：</td>
                        <td width="68%"><input type="text" style="width:70%" v-model="addUserName"></td>
                    </tr>
                    <tr>
                        <td align="right">密码：</td>
                        <td><input type="password" style="width:70%;margin-top:4px" v-model="addUserPsw"></td>
                    </tr>
                    <tr>
                        <td align="right">确认密码：</td>
                        <td><input type="password" style="width:70%;margin-top:4px" v-model="addUserPswTwo"></td>
                    </tr>
                    <tr>
                        <td align="right">权限：</td>
                        <td><input type="number" style="width:70%;margin-top:4px" v-model="permissions"></td>
                    </tr>
                    <tr>
                        <td align="right">管理员密码：</td>
                        <td><input type="password" style="width:70%;margin-top:4px" v-model="adminPsw"></td>
                    </tr>
                </table>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="submit">保 存</el-button>
                <el-button @click="dialogVisible = false">取 消</el-button>

            </span>
        </el-dialog>
        <el-dialog title="修改用户" :visible.sync="dialogVisiblerevise" :append-to-body="true" width="500px" :close-on-click-modal="false">
            <div>
                <table style="width:100%;">
                    <tr>
                        <td align="right" width="32%">用户名：</td>
                        <td width="68%">
                            <span>{{username}}</span>
                        </td>
                    </tr>
                    <tr>
                        <td align="right">新密码：</td>
                        <td><input type="password" style="width:70%;margin-top:4px" v-model="newPsw"></td>
                    </tr>
                    <tr>
                        <td align="right">确认新密码：</td>
                        <td><input type="password" style="width:70%;margin-top:4px" v-model="newPswTwo"></td>
                    </tr>
                    <tr>
                        <td align="right">权限：</td>
                        <td>
                            <span v-if="username=='admin'">{{quanxian}}</span>
                            <input v-else type="number" style="width:70%;margin-top:4px" v-model="quanxian">
                        </td>
                    </tr>
                    <tr>
                        <td align="right">管理员密码：</td>
                        <td><input type="password" style="width:70%;margin-top:4px" v-model="adminPsw1"></td>
                    </tr>
                </table>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="reviseInfo">保 存</el-button>
                <el-button @click="dialogVisiblerevise = false">取 消</el-button>

            </span>
        </el-dialog>
        <el-dialog title="修改用户" :visible.sync="dialogVisiblerevise1" :append-to-body="true" width="500px" :close-on-click-modal="false">
            <div>
                <table style="width:100%;">
                    <tr>
                        <td align="right" width="32%">用户名：</td>
                        <td width="68%">
                            <span>{{username}}</span>
                        </td>
                    </tr>
                    <tr>
                        <td align="right">原始密码：</td>
                        <td><input type="password" style="width:70%;margin-top:4px" v-model="originalPsw"></td>
                    </tr>
                    <tr>
                        <td align="right">新密码：</td>
                        <td><input type="password" style="width:70%;margin-top:4px" v-model="revisePsw"></td>
                    </tr>
                    <tr>
                        <td align="right">确认新密码：</td>
                        <td><input type="password" style="width:70%;margin-top:4px" v-model="revisePswTwo"></td>
                    </tr>
                    <tr>
                        <td align="right">权限：</td>
                        <td>
                            <span>{{quanxian}}</span>
                        </td>
                    </tr>

                </table>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="reviseInfo1">保 存</el-button>
                <el-button @click="dialogVisiblerevise1 = false">取 消</el-button>

            </span>
        </el-dialog>
        <el-dialog title="删除用户" :visible.sync="dialogVisibledelete" :append-to-body="true" width="500px" :close-on-click-modal="false">
            <div>
                <table style="width:100%;">
                    <tr>
                        <td align="right" width="32%">用户名：</td>
                        <td width="68%">
                            <span>{{username}}</span>
                        </td>
                    </tr>
                    <tr>
                        <td align="right">管理员密码：</td>
                        <td><input type="password" style="width:70%;margin-top:4px" v-model="adminPsw2"></td>
                    </tr>
                </table>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="deleteInfo()">确 定</el-button>
                <el-button @click="dialogVisibledelete = false">取 消</el-button>

            </span>
        </el-dialog>
    </div>

</template>

<script>
import screenfull from "screenfull";
export default {
    components: {},
    props: {},
    data() {
        return {
            tableData: [],
            dialogVisible: false,
            dialogVisiblerevise: false,
            dialogVisiblerevise1: false,
            dialogVisibledelete: false,
            username: "",
            quanxian: "",
            permissions: 255,
            clickIndex: "",
            clickrows: "",
            Priority: false,
            addUserName: "",
            addUserPsw: "",
            addUserPswTwo: "",
            adminPsw: "",
            adminPsw1: "",
            adminPsw2: "",
            revisePsw: "",
            revisePswTwo: "",
            newPsw: "",
            newPswTwo: "",
            originalPsw: ""
        };
    },
    watch: {},
    computed: {},
    methods: {
        addClick() {
            this.dialogVisible = true;
            this.addUserName = "";
            this.addUserPsw = "";
            this.addUserPswTwo = "";
            this.adminPsw = "";
        },
        handleClick(row, Priority) {
            if (Priority == true) {
                this.dialogVisiblerevise = true;
            } else {
                this.dialogVisiblerevise1 = true;
            }
            this.adminPsw1 = "";
            this.adminPsw2 = "";
            this.revisePsw = "";
            this.revisePswTwo = "";
            this.newPsw = "";
            this.newPswTwo = "";
            this.originalPsw = "";
            this.username = row.UserName;
            this.quanxian = row.priority;
            console.log(row);
        },
        //超级管理员修改
        reviseInfo() {
            var timestamp = new Date().valueOf();
            let username = sessionStorage.getItem("access_username");
            let sessionId = sessionStorage.getItem("access_token");
            if (this.newPsw != this.newPswTwo) {
                alert("两次输入的密码不一致，请重新输入");
                return false;
            }
            let regpsw = /^(\w){8,20}$/;
            if (!regpsw.test(this.newPsw)) {
                alert("请输入正确格式密码");
                return false;
            }
            if (this.username != "admin") {
                let regpermissions = /^[0-9]*[1-9][0-9]*$/;
                if (
                    !regpermissions.test(this.quanxian) ||
                    this.quanxian < 1 ||
                    this.quanxian > 255
                ) {
                    alert("请输入正确权限值");
                    return false;
                }
            }
            let aoData = {
                Authentication: {
                    UserName: username,
                    SessionId: sessionId
                },
                data: [
                    {
                        CommandType: "set",
                        CommandData: {
                            CommandName: "UpdateUser",
                            Auth: {
                                UserName: username,
                                password: this.adminPsw1
                            },
                            UserInfo: {
                                UserName: this.username,
                                password: this.newPsw,
                                priority: this.quanxian
                            }
                        }
                    }
                ],
                Protocol: {
                    Name: "liguoNetWork",
                    Version: "1.0.0"
                },
                JSON: {
                    Version: "1.0.0"
                },
                datalen: 1,
                request: timestamp,
                timestamp: timestamp
            };
            let that = this;
            this.$axios
                .post("/cmd", aoData)
                .then(function(response) {
                    if (response.data.status == "success") {
                        alert("修改成功");
                        if (that.username == "admin") {
                            sessionStorage.clear();
                            that.$router.replace("/pages/login");
                            if (document.exitFullscreen) {
                                document.exitFullscreen();
                            } else if (document.webkitCancelFullScreen) {
                                document.webkitCancelFullScreen();
                            } else if (document.mozCancelFullScreen) {
                                document.mozCancelFullScreen();
                            } else if (document.msExitFullscreen) {
                                document.msExitFullscreen();
                            }
                        } else {
                            that.dialogVisiblerevise = false;
                            that.getList();
                        }
                    } else if (response.data.status == "fail") {
                        alert(response.data.error.errorstring.cn);
                    }
                })
                .catch(function(error) {
                    console.log(error);
                });
        },
        //普通用户修改
        reviseInfo1() {
            if (this.revisePsw != this.revisePswTwo) {
                alert("两次输入的密码不一致，请重新输入");
                return false;
            }
            let regpsw = /^(\w){8,20}$/;
            if (!regpsw.test(this.revisePsw)) {
                alert("请输入正确格式密码");
                return false;
            }
            var timestamp = new Date().valueOf();
            let username = sessionStorage.getItem("access_username");
            let sessionId = sessionStorage.getItem("access_token");
            let aoData = {
                Authentication: {
                    UserName: username,
                    SessionId: sessionId
                },
                data: [
                    {
                        CommandType: "set",
                        CommandData: {
                            CommandName: "UpdateUser",
                            Auth: {
                                UserName: username,
                                password: this.originalPsw
                            },
                            UserInfo: {
                                UserName: this.username,
                                password: this.revisePsw,
                                priority: this.quanxian
                            }
                        }
                    }
                ],
                Protocol: {
                    Name: "liguoNetWork",
                    Version: "1.0.0"
                },
                JSON: {
                    Version: "1.0.0"
                },
                datalen: 1,
                request: timestamp,
                timestamp: timestamp
            };
            let that = this;
            this.$axios
                .post("/cmd", aoData)
                .then(function(response) {
                    if (response.data.status == "success") {
                        alert("修改成功");
                        sessionStorage.clear();
                        that.$router.replace("/pages/login");
                        if (document.exitFullscreen) {
                            document.exitFullscreen();
                        } else if (document.webkitCancelFullScreen) {
                            document.webkitCancelFullScreen();
                        } else if (document.mozCancelFullScreen) {
                            document.mozCancelFullScreen();
                        } else if (document.msExitFullscreen) {
                            document.msExitFullscreen();
                        }
                    } else if (response.data.status == "fail") {
                        alert(response.data.error.errorstring.cn);
                    }
                })
                .catch(function(error) {
                    console.log(error);
                });
        },

        deleteRow(index, row, rows) {
            this.dialogVisibledelete = true;
            this.username = row.UserName;
            this.clickIndex = index;
            console.log(this.clickIndex);
            this.clickrows = rows;
        },
        //删除用户
        deleteInfo() {
            var timestamp = new Date().valueOf();
            let username = sessionStorage.getItem("access_username");
            let sessionId = sessionStorage.getItem("access_token");
            let aoData = {
                Authentication: {
                    UserName: username,
                    SessionId: sessionId
                },
                data: [
                    {
                        CommandType: "set",
                        CommandData: {
                            CommandName: "DeleteUser",
                            Auth: {
                                UserName: username,
                                password: this.adminPsw2
                            },
                            UserName: this.username
                        }
                    }
                ],
                Protocol: {
                    Name: "liguoNetWork",
                    Version: "1.0.0"
                },
                JSON: {
                    Version: "1.0.0"
                },
                datalen: 1,
                request: timestamp,
                timestamp: timestamp
            };
            let that = this;
            this.$axios
                .post("/cmd", aoData)
                .then(function(response) {
                    if (response.data.status == "success") {
                        alert("删除成功");
                        that.dialogVisibledelete = false;
                        that.getList();
                    } else if (response.data.status == "fail") {
                        alert(response.data.error.errorstring.cn);
                    }
                })
                .catch(function(error) {
                    console.log(error);
                });
        },
        //添加用户
        submit() {
            if (this.addUserPsw != this.addUserPswTwo) {
                alert("两次输入的密码不一致，请重新输入");
                return false;
            }
            let regname = /^(\w){5,15}$/;
            if (!regname.test(this.addUserName)) {
                alert("请输入正确格式账户名");
                return false;
            }
            let regpsw = /^(\w){8,20}$/;
            if (!regpsw.test(this.addUserPsw)) {
                alert("请输入正确格式密码");
                return false;
            }
            let regpermissions = /^[0-9]*[1-9][0-9]*$/;
            if (
                !regpermissions.test(this.permissions) ||
                this.permissions < 1 ||
                this.permissions > 255
            ) {
                alert("请输入正确权限值");
                return false;
            }
            var timestamp = new Date().valueOf();
            let username = sessionStorage.getItem("access_username");
            let sessionId = sessionStorage.getItem("access_token");
            let aoData = {
                Authentication: {
                    UserName: username,
                    SessionId: sessionId
                },
                data: [
                    {
                        CommandType: "set",
                        CommandData: {
                            CommandName: "AddUser",
                            Auth: {
                                UserName: username,
                                password: this.adminPsw
                            },
                            UserInfo: {
                                UserName: this.addUserName,
                                password: this.addUserPsw,
                                priority: this.permissions
                            }
                        }
                    }
                ],
                Protocol: {
                    Name: "liguoNetWork",
                    Version: "1.0.0"
                },
                JSON: {
                    Version: "1.0.0"
                },
                datalen: 1,
                request: timestamp,
                timestamp: timestamp
            };
            let that = this;
            this.$axios
                .post("/cmd", aoData)
                .then(function(response) {
                    if (response.data.status == "success") {
                        alert("添加成功");
                        that.dialogVisible = false;
                        that.getList();
                    } else if (response.data.status == "fail") {
                        alert(response.data.error.errorstring.cn);
                    }
                })
                .catch(function(error) {
                    console.log(error);
                });
        },
        //获取用户列表
        getList() {
            var timestamp = new Date().valueOf();
            let username = sessionStorage.getItem("access_username");
            let sessionId = sessionStorage.getItem("access_token");
            let aoData = {
                Authentication: {
                    UserName: username,
                    SessionId: sessionId
                },
                data: [
                    {
                        CommandType: "get",
                        CommandData: {
                            CommandName: "User",
                            UserName: username
                        }
                    }
                ],
                Protocol: {
                    Name: "liguoNetWork",
                    Version: "1.0.0"
                },
                JSON: {
                    Version: "1.0.0"
                },
                datalen: 1,
                request: timestamp,
                timestamp: timestamp
            };
            let that = this;
            this.$axios
                .post("/cmd", aoData)
                .then(function(response) {
                    if (response.data.status == "success") {
                        that.tableData = response.data.data[0];
                    } else if (response.data.status == "fail") {
                        alert(response.data.error.errorstring.cn);
                    }
                })
                .catch(function(error) {
                    console.log(error);
                });
        }
    },
    created() {},
    mounted() {
        this.$nextTick(function() {
            if (sessionStorage.getItem("access_priority") == "0") {
                this.Priority = true;
            }
            this.getList();
        });
        let that = this;
        window.myInterval = setInterval(function() {
            that.getList();
        }, 2000);
    }
};
</script>
<style>
.el-table .cell,
.el-table th div,
.el-table--border td:first-child .cell,
.el-table--border th:first-child .cell {
    padding-left: 60px;
}
</style>
<style lang="scss" scoped>
.wrapper {
    height: 100%;
    padding: 10px;
}
.content {
    width: 800px;
    overflow: auto;
    margin: 0 auto;
    height: calc(100% - 20px);
    background: white;
    box-shadow: 2px 2px 2px #ccc;
}
.adminTable {
}
</style>



